@import url(https://fonts.googleapis.com/css?family=Playfair+Display+SC:700|Open+Sans:300,700&display=swap);
:root {
    --color-primary: #FFEF78;
    --color-primary-rgb: 255, 239, 120;
    --color-dark: #000000;
    --color-dark-rgb: 0, 0, 0;
    --color-light: #FFFFFF;
    --color-light-rgb: 255, 255, 255;
    --font-family-primary: "Open Sans", "Helvetica Neue", "sans-serif";
    --font-family-secondary: "Otama EP", "serif";
}
.button {
    border: 0;
    border-radius: 24px;
    cursor: pointer;
    display: inline-block;
    font-family: var(--font-family-primary);
    font-size: 16px;
    font-weight: 600;
    outline: none;
    padding-bottom: 8px;
    padding-left: 24px;
    padding-right: 24px;
    padding-top: 8px;
    text-transform: uppercase;
    -webkit-transition: all 300ms;
    transition: all 300ms;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.button:hover,
.button:active,
.button:focus {
    outline: none;
}

.button.button--primary {
    background-color: var(--color-dark);
    border: 1px solid var(--color-dark);
    color: var(--color-light);
}

.button:hover {
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
}

.button.button--default {
    background-color: var(--color-primary);
    border: 1px solid var(--color-dark);
    color: var(--color-dark);
}

::-moz-selection {
  background-color: var(--color-dark);
  color: var(--color-light);
}

::selection {
  background-color: var(--color-dark);
  color: var(--color-light);
}

body {
  font-family: var(--font-family-primary); 
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  scroll-behavior: smooth;  
}

html,
body {
  background-color: var(--color-primary);
  font-weight: 300;
  margin-bottom: 0;
  margin-left: 0;
  margin-right: 0;
  margin-top: 0;
  padding-bottom: 0;
  padding-left: 0;
  padding-right: 0;
  padding-top: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-family-secondary);
  text-transform: uppercase;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin-bottom: 0;
  margin-left: 0;
  margin-right: 0;
  margin-top: 0;
}

p {
  font-size: 16px;
}

@font-face {
  font-family: 'Otama EP';
  src: url(/static/media/otama-ep-webfont.2ca58e3c.woff2) format('woff2'),
       url(/static/media/otama-ep-webfont.31108306.woff) format('woff');
  font-weight: normal;
  font-style: normal;
}

@media (min-width: 1024px) {
  ::-webkit-scrollbar {
    width: 8px;
  }
  
  ::-webkit-scrollbar-track:hover {
    background-color: rgba(var(--color-dark-rgb), 0.2); 
  }
  
  ::-webkit-scrollbar-thumb {
    background-color: rgba(var(--color-dark-rgb), 0.2); 
    border-radius: 16px;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background-color: rgba(var(--color-dark-rgb), 0.4);
  }  
}
.App {
  text-align: center;
}
.gift {
    background-color: var(--color-primary);
    border: 1px solid var(--color-dark);
    border-radius: 4px;
    color: var(--color-dark);
    box-sizing: border-box;
    display: block;
    padding-bottom: 16px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 16px;
    text-decoration: none;
    -webkit-transition: all 304ms;
    transition: all 304ms;
    width: 280px;
}

.gift:hover {
    -webkit-transform: scale(1.04);
            transform: scale(1.04);
}

.gift .icon,
.gift .price,
.gift .aprox-price {
    display: block;
}

.gift .icon {
    background-repeat: no-repeat;
    background-size: contain;
    height: 98px;
    margin: 0 auto;
    padding-bottom: 8px;
    width: 98px;
}

.gift h3 {
    font-family: var(--font-primary);
    font-size: 16px;
    font-weight: 300;
    padding-bottom: 16px;
    text-transform: none;
}

.gift .price {
    font-size: 20px;
    font-weight: 600;
    padding-bottom: 8px;
}

.gift .aprox-price {
    font-size: 14px;
}

@media (min-width: 1024px) {
    .gift {
        width: 200px;
    }
}
.modal {
    background-color: var(--color-primary);
    box-shadow: 0 8px 16px rgba(var(--color-dark-rgb), 0.2);
    box-sizing: border-box;
    height: 100vh;
    left: 0;
    padding-bottom: 16px;
    padding-top: 16px;
    position: fixed;
    top: 0;
    -webkit-transition: all 400ms;
    transition: all 400ms;
    width: 100%;
}

.modal.is--hidden {
    -webkit-transform: translateY(110%);
            transform: translateY(110%);
}

.modal--is-visible {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.modal .modal__header {
    background-color: var(--color-primary);
    box-sizing: border-box;
    display: flex;
    justify-content: flex-end;
    left: 0;
    top: 0;
    padding-bottom: 16px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 16px;
    position: absolute;
    -webkit-transition: box-shadow 304ms;
    transition: box-shadow 304ms;
    width: 100%;
    z-index: 1;
}

.modal__header.with--box-shadow {
    box-shadow: 0 4px 8px rgba(var(--color-dark-rgb), 0.2);
}

.modal .modal__content {
    height: calc(100vh - 56px);
    margin-top: 38px;
    overflow-y: scroll;
    padding-left: 32px;
    padding-right: 32px;    
    -webkit-overflow-scrolling: touch;
}

.button--close {
    background-color: transparent;
    border: 0;
    cursor: pointer;
    padding-left: 0;
    padding-right: 0;
}

.button--close,
.button-close:active,
.button--close:hover,
.button--close:focus {
    outline: none;
}

.button--close svg {
    height: 16px;
    fill: var(--color-dark);
    width: 16px;
}

@media (min-width: 1024px) {
    .modal {
        left: auto;
        padding-bottom: 24px;
        padding-top: 24px;
        right: 0;
        width: 30%;
    }

    .modal .modal__content {
        height: calc(100vh - 64px);
        margin-top: 40px;
        padding-left: 24px;
        padding-right: 24px;        
    }

    .modal .modal__content h2 {
        margin-top: 16px;
    }

    .modal .modal__header {
        padding-bottom: 24px;
        padding-left: 24px;
        padding-right: 24px;
        padding-top: 24px;
    }

    .modal.is--hidden {
        -webkit-transform: translateX(110%);
                transform: translateX(110%);
    }
    
    .modal--is-visible {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }    
}
.playlist {
    align-items: center;
    background-color: transparent;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    left: 16px;
    position: -webkit-sticky;
    position: sticky;
    top: calc(100% - 48px - 32px);
    -webkit-transition: -webkit-transform 400ms;
    transition: -webkit-transform 400ms;
    transition: transform 400ms;
    transition: transform 400ms, -webkit-transform 400ms;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.playlist:hover {
    -webkit-transform: translateX(6px);
            transform: translateX(6px);
}

.button--spotify {
    align-items: center;
    background-color: var(--color-dark);
    border: 0;
    border-radius: 100%;
    box-shadow: 0 4px 8px rgba(var(--color-dark-rgb), 0.2);
    cursor: pointer;
    display: flex;
    justify-content: center;
    padding-bottom: 16px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 16px;
}

.button--spotify,
.button--spotify:active,
.button--spotify:hover,
.button--spotify:focus {
    outline: none;
}

.button--spotify svg.logo {
    fill: var(--color-primary);
    height: 32px;
    margin-top: 0;
    width: 32px;
}

.button--spotify .label {
    display: none;
}

@media (min-width: 1024px) {
    .playlist {
        display: flex;
        height: 72px;
        top: 50%;
        -webkit-transform: translateX(-105px);
                transform: translateX(-105px);
        position: absolute;
        width: 98px;
    }
    
    .playlist:hover {
        -webkit-transform: translateX(40%);
                transform: translateX(40%);
    }
    
    .button--spotify {
        background-color: var(--color-primary);
        border-radius: 0 4px 4px 0;
        padding-bottom: 8px;
        padding-top: 8px;
    }

    .button--spotify svg.logo {
        fill: var(--color-dark);
        height: 32px;
        margin-left: 6px;
        width: 32px;
    }

    .button--spotify .label {
        display: block;
        flex: 1 1;
        font-weight: 600;
        margin-right: 8px;
        white-space: nowrap;
    }    
}
.home {
    display: flex;
    flex-direction: column;
}

.picture,
.sidebar {
    box-sizing: border-box;
    width: 100%;
}

.picture {
    align-items: center;
    background-blend-mode: multiply;
    background-color: var(--color-primary);
    background-image: url(https://ucarecdn.com/62d49504-9de0-4a02-b7d8-a2be71a26599/-/progressive/yes/-/quality/lightest/pamellucas.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 150%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100vh;
    height: calc(1vh * 100);
    height: calc(var(--vh, 1vh) * 100);
    padding-bottom: 32px;
    padding-left: 32px;
    padding-right: 32px;
    padding-top: 32px;
}

.picture .logo {
    fill: var(--color-light);
    margin-top: -56px;
}

.button--details {
    align-items: center;
    display: flex;
    justify-content: center;
}

.button--details .chevron-bottom {
    fill: var(--color-light);
    height: 12px;
    margin-left: 8px;
    width: 12px;
}

.sidebar {
    background-color: var(--color-primary);
    padding-bottom: 32px;
    padding-left: 16px;
    padding-right: 16px;
    padding-top: 32px;
}

.sidebar .information:first-child {
    padding-top: 0;
}

.sidebar .information:last-child:after {
    display: none;
}

.information__container {
    margin-bottom: 80px;
    margin-top: -64px;
}

.information {
    padding-top: 32px;
}

.information:after {
    background-color: var(--color-dark);
    content: "";
    display: block;
    height: 1px;
    margin-top: 32px;
    width: 100%;
}

.information h2 {
    font-size: 26px;
    padding-bottom: 24px;
}

.information p {
    padding-bottom: 8px;
}

.information p:last-child {
    padding-bottom: 0;
}

.information .button {
    margin-top: 24px;
}

.modal__content h2 {
    padding-bottom: 24px;
}

.gift__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 32px;
    margin-top: 48px;
}

.gift__list .gift {
    margin-bottom: 48px;
}


@media (min-width: 1024px) {
    .home {
        flex-direction: row;
    }

    .picture {
        background-size: cover;
        width: 70%;
    }

    .picture .logo {
        margin-top: -140px;
        width: 560px;
    }

    .picture .menu,
    .picture .button {
        display: none;
    }

    .sidebar {
        height: 100vh;
        overflow-y: scroll;
        padding-bottom: 64px;
        padding-left: 64px;
        padding-right: 64px;
        padding-top: 64px;
        width: 30%;
    }

    .information {
        padding-top: 48px;
    }

    .information:after {
        margin-top: 48px;
    }

    .information__container {
        margin-bottom: 0;
        margin-top: 0;
    }

    .gift__list .gift {
        margin-bottom: 24px;
        margin-left: 12px;
        margin-right: 12px;
    }    
}
.thank-you {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    margin-top: -48px;
    padding-bottom: 32px;
    padding-left: 32px;
    padding-right: 32px;
    padding-top: 32px;
}

.thank-you .hearts svg {
    fill: var(--color-light);
    width: 120px;
}

.thank-you .hearts .heart {
    -webkit-animation: pop 1000ms infinite;
            animation: pop 1000ms infinite;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.thank-you .hearts .heart:nth-child(1) {
    -webkit-animation-delay: 0.6s;
            animation-delay: 0.6s;
}

.thank-you .hearts .heart:nth-child(2) {
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}

.thank-you .hearts .heart:nth-child(3) {
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}

.thank-you .content {
    margin-top: -16px;
}

.thank-you .content h1 {
    font-size: 32px;
    margin-bottom: 16px;
}

.thank-you .content p {
    font-size: 18px;
}

.thank-you .content .button {
    margin-top: 32px;
}

@-webkit-keyframes pop {
    from {
        opacity: 0;
        -webkit-transform: translateY(20%) scale(0.2);
                transform: translateY(20%) scale(0.2);
    }

    to {
        opacity: 1;
        -webkit-transform: translateY(0) scale(1);
                transform: translateY(0) scale(1);
    }
}

@keyframes pop {
    from {
        opacity: 0;
        -webkit-transform: translateY(20%) scale(0.2);
                transform: translateY(20%) scale(0.2);
    }

    to {
        opacity: 1;
        -webkit-transform: translateY(0) scale(1);
                transform: translateY(0) scale(1);
    }
}

@media (min-width: 1024px) {
    .thank-you .hearts svg {
        width: 240px;
    }

    .thank-you .content {
        margin-top: -32px;
    }    

    .thank-you .content h1 {
        font-size: 56px;
    }

    .thank-you .content p {
        font-size: 24px;
    }

    .thank-you .content .button {
        margin-top: 48px;
    }    
}
